@font-face {
  font-family: "icomoons";
  src: url("../fonts/icomoon/fonts/icomoon.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"],
[class*=" icon-"] {
  font-family: "icomoons" !important;
  speak-as: none;
  font-weight: normal;
  font-style: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-tripadvisor:before {
  content: "\e900";
}

.icon-twitter:before {
  content: "\ea96";
}

.icon-facebook:before {
  content: "\ea90";
}

.icon-instagram:before {
  content: "\ea92";
}

.icon-keyboard_arrow_down:before {
  content: "\e901";
}

.icon-circle-right:before {
  content: "\ea42";
}

.icon-circle-left:before {
  content: "\ea44";
}

.icon-calendar:before {
  content: "\e953";
}

.icon-clock:before {
  content: "\e94e";
}

.icon-keyboard_arrow_up:before {
  content: "\e901";
}

@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato/Lato-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Lato-Black";
  src: url("../fonts/Lato/Lato-Black.ttf") format("truetype"), url("Lato-Black.woff2") format("woff2"),
    url("Lato-Black.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Old Standard TT";
  src: url("../fonts/Old_Standard_TT/OldStandard-Regular.ttf") format("truetype"),
    url("OldStandard-Regular.woff2") format("woff2"), url("OldStandard-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Old Standard TT Black";
  src: url("../fonts/Old_Standard_TT/OldStandard-Bold.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Silhouetto-Script";
  src: url("../fonts/Silhouetto-Script/Silhouetto-Regular.ttf") format("truetype"),
    url("Silhouetto-Regular.otf") format("opentype"), url("Silhouetto-Regular.woff2") format("woff2"),
    url("Silhouetto-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
